.about {
    text-align: center;
    padding: 20px;
    font-family: 'Roboto', sans-serif;
}
  
.about h1 {
    font-size: 2.5em;
    margin-bottom: 20px;
}
  
.about-content {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 20px; /* Reduced the gap */
    margin-top: 20px;
}
  
.about-left {
    flex: 1;
    margin-left: 200px; /* Adjusted margin */
}
  
.about-right {
    flex: 2;
    text-align: left;
    max-width: 600px;
    margin-right: 150px; /* Adjusted margin */
    margin-top: 50px;
    
}
  
.about-img {
    border-radius: 1%;
    width: 362px;
    height: 432px;
}
  
.footer {
    background-color: #0ec9b9;
    color: black;
    text-align: center;
    padding: 10px 0;
    position: fixed;
    width: 100%;
    bottom: 0;
}
